export default {
    data() {
        return {
            hotelOption: [],
            priceScope: 4000,
            // ÈÖíÂ∫óÁ≠âÁ∫ß
            levels:'',
            // ÈÖíÂ∫óÁ±ªÂûã
            types:'',
            // ÈÖíÂ∫óËÆæÊñΩ
            assets:'',
            // ÈÖíÂ∫óÂìÅÁâå
            brands:'',
            isLevels:'‰∏çÈôê',
            isTypes:'‰∏çÈôê',
            isAssets:'‰∏çÈôê',
            isBrands:'‰∏çÈôê',
            levelsIndex:[],
            typesIndex:[],
            assetsIndex:[],
            brandsIndex:[],
            // ÊéßÂà∂ÊâÄÊúâÁöÑÂ§öÈÄâÊ°ÜÁöÑÈÄâ‰∏≠Áä∂ÊÄÅ
            isChecked: false
        }
    },
    created(){
         // Ëé∑ÂèñÈÖíÂ∫óÈÄâÈ°π
        this.$axios({
            url:'/hotels/options'
        }).then(res =>{
            // console.log(res.data.data);
            this.hotelOption = res.data.data
        })
    },
    methods:{
        // ËøôÈáåÁªü‰∏ÄÂ§ÑÁêÜÁ≠õÈÄâÂèëÁîüÂèòÂåñÂèëÈÄÅËØ∑Ê±Ç
        getHotelList(){
            const hotelOption = {
                'price_lt': this.priceScope,
                'hotellevel_in': this.levelsIndex,
                'hoteltype_in': this.typesIndex,
                'hotelasset_in': this.assetsIndex,
                'hotelbrand_in': this.brandsIndex
            }
            this.$emit('getHotelList',hotelOption)
        },
        // ‰ª∑Ê†ºÂå∫Èó¥ÂèëÁîüÂèòÂåñ
        priceChange(newPrice,isGet){
            // console.log(priceScope);
            if(!isGet){
                this.getHotelList()
            }
        },
        // ‰ΩèÂÆøÁ≠âÁ∫ßÊï∞ÁªÑÂèëÁîüÂèòÂåñ
        levelsChange(newLevels,isGet){
            if(!newLevels.length){
                this.isLevels = '‰∏çÈôê'
            }else if(newLevels.length == 1){
                this.hotelOption.levels.forEach(item =>{
                    if(item.id == newLevels[0]){
                        this.isLevels = item.name
                        return
                    }
                })
            }else{
                this.isLevels = `Â∑≤ÈÄâ${newLevels.length}È°π`
            }
            // console.log(newLevels);
            if(!isGet){
                this.getHotelList()
            }
        },
        // ‰ΩèÂÆøÁ±ªÂûãÊï∞ÁªÑÂèëÁîüÂèòÂåñ
        typesChange(newTypes,isGet){
            if(!newTypes.length){
                this.isTypes = '‰∏çÈôê'
            }else if(newTypes.length == 1){
                this.hotelOption.types.forEach(item =>{
                    if(item.id == newTypes[0]){
                        this.isTypes = item.name
                        return
                    }
                })
            }else{
                this.isTypes = `Â∑≤ÈÄâ${newTypes.length}È°π`
            }
            // console.log(newTypes);
            if(!isGet){
                this.getHotelList()
            }
        },
        // ÈÖíÂ∫óËÆæÊñΩÊï∞ÁªÑÂèëÁîüÂèòÂåñ
        assetsChange(newAssets,isGet){
            if(!newAssets.length){
                this.isAssets = '‰∏çÈôê'
            }else if(newAssets.length == 1){
                this.hotelOption.assets.forEach(item =>{
                    if(item.id == newAssets[0]){
                        this.isAssets = item.name
                        return
                    }
                })
            }else{
                this.isAssets = `Â∑≤ÈÄâ${newAssets.length}È°π`
            }
            // console.log(newAssets);
            if(!isGet){
                this.getHotelList()
            }
        },
        // ÈÖíÂ∫óÂìÅÁâåÊï∞ÁªÑÂèëÁîüÂèòÂåñ
        brandsChange(newBrands,isGet){
            if(!newBrands.length){
                this.isBrands = '‰∏çÈôê'
            }else if(newBrands.length == 1){
                this.hotelOption.brands.forEach(item =>{
                    if(item.id == newBrands[0]){
                        this.isBrands = item.name
                        return
                    }
                })
            }else{
                this.isBrands = `Â∑≤ÈÄâ${newBrands.length}È°π`
            }
            // console.log(newBrands);
            if(!isGet){
                this.getHotelList()
            }
        },
        // Êí§ÈîÄÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂
        repeal(){
            // Â∞Ü‰ª∑Ê†ºÂå∫Èó¥ÂèòÂõûÊúÄÂºÄÂßãÁöÑ4000
            this.priceScope = 4000
            // ÂèñÊ∂àÈÄâ‰∏≠ÁöÑÊï∞ÊçÆÔºåÂπ∂ÊâãÂä®Ëß¶Âèë‰∏ÄÊ¨°Êï∞ÊçÆÂèòÂåñ‰∫ã‰ª∂
            this.levelsChange(this.levelsIndex = [],true)
            this.typesChange(this.typesIndex = [],true)
            this.assetsChange(this.assetsIndex = [],true)
            this.brandsChange(this.brandsIndex = [],true)
            this.getHotelList()
        }
    }
}
