export default {
    data() {
        return {
            visible: false,
            // Âá∫ÂèëÂú∞
            formCityName:'',
            // Âá∫ÂèëÊó∂Èó¥
            arrDate:[],
            // ‰∫∫Êï∞
            peopleNumber:'',
            // Êàê‰∫∫Êï∞
            adultValue: '',
            // ÂÑøÁ´•Êï∞
            kidValue: '',
            pickerOptions:{
                // Á¶ÅÁî®Êó•Êúü
                disabledDate(disDate){
                    return disDate.getTime() <= (Date.now()-86400000)
                },
            }

        }
    },
    methods:{
        // ÈÄâ‰∏≠ÊêúÁ¥¢Âª∫ËÆÆÂõûË∞ÉÂáΩÊï∞
        SelectCity(item){
            // ÈÄâ‰∏≠Êó∂ÂèØ‰ª•ÊãøÂà∞ÈÄâ‰∏≠ÁöÑÈÇ£‰∏™Êï∞ÊçÆÂØπË±°
            if(!item){
                let item = new Object()
                item.name = this.formCityName
                // const item = {'name': this.formCityName}
            }
            this.$axios({
                url:'/cities?name=' + this.formCityName
            }).then(res => {
                if(!res.data.data.length){
                    this.$message({
                        showClose: true,
                        message: `ÊêúÁ¥¢‰∏çÂà∞ÂΩìÂâçÂüéÂ∏Ç`,
                        type: 'error'
                    });
                    return
                }
                const city = res.data.data[0].id
                let item = {}
                item.name = this.formCityName
                this.$emit('getHotelList',{city,'cityName': item.name})
            }).catch(err => {
                console.log(err);
            })
            
            
        },

        // ÊòæÁ§∫Âª∫ËÆÆÂàóË°®ÂáΩÊï∞ÔºåÂú®ËæìÂÖ•Ê°Ü‰∏≠ËæìÂÖ•Êó∂Ëß¶Âèë 
        querySearchAsync(queryString , showList){
            // Âà§Êñ≠ÊòØÂê¶Â≠òÂú®Êï∞ÊçÆ
            if (!queryString) {
                showList([
                    {
                        value: "ËØ∑ËæìÂÖ•ÂÖ≥ÈîÆÂ≠ó",
                    },
                ]);
                return;
            }
            // ËøúÁ®ãÊúçÂä°Âô®Ëé∑ÂèñÂüéÂ∏ÇÂàóË°®
            this.$axios({
                url: "/airs/city",
                params: {
                    name: queryString,
                },
            }).then((res) => {
                // console.log(res.data);
                const data = res.data.data.map((city) => {
                    // ‰πãÂâçËøôÈáåÊòØÁõ¥Êé•Ëé∑ÂèñÊï∞ÊçÆ, value ÊòØ‰ª• name ‰Ωú‰∏∫Âü∫Á°Ä
                    // Áî®Êù•ÊòæÁ§∫Âª∫ËÆÆÂàóË°®, Âè™ÈúÄË¶ÅÂú®ËµãÂÄº value ÁöÑÊó∂ÂÄô,Â∞Ü Â∏ÇÂ≠óÂéªÊéâÂç≥ÂèØ
                    return {
                        ...city,
                        value: city.name,
                    };
                });
                showList(data);
            });
        },
        // Á°ÆËÆ§ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂
        headleClick(){
            this.peopleNumber = `${this.adultValue}Êàê‰∫∫ ${this.kidValue}ÂÑøÁ´•`
            this.visible = false
        }
    },
    watch:{
        arrDate(){
            // Â∞ÜÂÖ•‰ΩèÂíåÁ¶ªÂ∫óÊó∂Èó¥ÁöÑÊï∞ÁªÑËøõË°åËß£ÊûÑËµãÂÄº
            const [enterTime,leftTime] = this.arrDate
            this.$emit('getHotelList',{enterTime,leftTime})
        }
    }
}
