export default {
    data() {
        return {
            isBox: true,
            isGo: false,
            isMap: true,
            cityId: '',
            loading: false,
            hotelList: {
                data: []
            },
            currentPage: 1,
            // Ëé∑ÂèñÁöÑÊù°Êï∞
            limit: 10,
            hotelOption: {},
            urlCityName: '',
            cities: [],
            // ÂΩìÂâçÊêúÁ¥¢È°µÂüéÂ∏ÇÁªèÁ∫¨Â∫¶
            location: [],
            map: {},
            markers: []
        }
    },
    created() {
        
        this.loading = true
        this.isMap = true
    },

    async mounted() {
        window.onLoad = async () => {
            var map = new AMap.Map("container", {
                zoom: 7, // Á∫ßÂà´
                center: [113.428072, 23.129259], // ‰∏≠ÂøÉÁÇπÂùêÊ†á
                viewMode: "3D", // ‰ΩøÁî®3DËßÜÂõæ
                autoFitView: true, // ÊòØÂê¶Ëá™Âä®Ë∞ÉÊï¥Âú∞ÂõæËßÜÈáé‰ΩøÁªòÂà∂ÁöÑ MarkerÁÇπÈÉΩÂ§Ñ‰∫éËßÜÂè£ÁöÑÂèØËßÅËåÉÂõ¥
            });
            this.map = map;
            if (!this.$route.query.cityName) {
                await this.getInfoCity()
            } else {
                // Ëé∑ÂèñÂüéÂ∏ÇÂêç
                this.urlCityName = this.$route.query.cityName

                // Ëé∑ÂèñÂüéÂ∏Çid
                await this.$axios({
                    url: '/cities?name=' + this.$route.query.cityName
                }).then(res => {
                    if (!res.data.data.length) {
                        this.$message({
                            showClose: true,
                            message: `ÊêúÁ¥¢‰∏çÂà∞ÂΩìÂâçÂüéÂ∏Ç`,
                            type: 'error'
                        });
                        return
                    }
                    // console.log(res.data.data[0].id);
                    this.cityId = res.data.data[0].id
                })

                this.getHotelList()
                this.getScenice()
            }

        };

        var key = "d5192dea5a16faf3b3afdd0fb562d794"; // ‰Ω†ÁöÑkey
        var url = `https://webapi.amap.com/maps?v=1.4.15&key=${key}&callback=onLoad`;
        var jsapi = document.createElement('script');
        jsapi.charset = 'utf-8';
        jsapi.src = url;
        document.head.appendChild(jsapi);
    },
    methods: {
        // Ëé∑ÂèñÊôØÁÇπ
        getScenice() {
            // Ëé∑ÂèñÂüéÂ∏ÇÂå∫ÂüüÊôØÁÇπ
            this.$axios({
                url: `/cities?name=${this.urlCityName}`
            }).then(res => {
                // console.log(res.data.data[0].scenics);
                this.cities = res.data.data[0].scenics
            })
        },

        // Ëé∑ÂèñÈÖíÂ∫óÂàóË°®
        async getHotelList(hotelOption) {
            if(this.HotelList){
                this.isBox = false
            }
            this.loading = true
            // Áî®‰∫éÂú∞ÂùÄÊ†èÊòæÁ§∫
            let str = "";
            if (hotelOption) {
                console.log(hotelOption);
                // Â∞ÜËé∑ÂèñËøáÊù•ÁöÑÊï∞ÊçÆËøõË°åÊãºÊé•
                var keys = Object.keys(hotelOption); // ["city", "price_lt", "hotellevel", "hoteltype"]

                keys.forEach(Option => {
                    // Êï∞ÊçÆÁöÑÊ†ºÂºèÊòØÂê¶‰∏∫Êï∞ÁªÑ
                    if (Array.isArray(hotelOption[Option])) {
                        hotelOption[Option].forEach(item => {
                            str += `${Option}=${item}&`
                        })
                    } else {
                        // Âà§Êñ≠‰º†ÈÄíËøáÊù•ÁöÑÊï∞ÂÄº‰∏≠ÊúâÊ≤°ÊúâÂüéÂ∏ÇÂêçÂ≠óÂíåÂüéÂ∏Çid
                        if (Option == 'cityName') {
                            this.urlCityName = hotelOption[Option]

                            // ‰øÆÊîπÂéüÊúâÂèÇÊï∞,ËÄå‰∏çË∑≥ËΩ¨È°µÈù¢
                            this.$router.push({
                                path: this.$route.path,
                                query: Object.assign({}, this.$route.query, { cityName: this.urlCityName })
                            })

                            // ÈáçÊñ∞Ëé∑ÂèñÊôØÁÇπ‰ø°ÊÅØ
                            this.getScenice()
                        } else if (Option == 'city') {
                            this.cityId = hotelOption[Option]
                        } else {
                            str += `${Option}=${hotelOption[Option]}&`;
                        }

                    }
                })
                // ÂÆö‰ΩçÂà∞Á¨¨‰∏ÄÈ°µÊòæÁ§∫
                this.currentPage = 1
            }
            // str += `_limit=${this.limit}`
            str += `city=${this.cityId}&_limit=${this.limit}`

            console.log(str);
            // ÂèëÈÄÅËØ∑Ê±ÇËé∑ÂèñÊï∞ÊçÆ
            const HotelList = await this.$axios({
                url: `/hotels?${str}`,
            })

            HotelList.data.data = HotelList.data.data.slice((this.currentPage - 1) * 10, (this.currentPage) * 10)
            this.hotelList = HotelList.data
            this.loading = false
            if (!this.hotelList.data.length) {
                this.isGo = true
                this.isBox = true
            } else {
                this.isBox = false
            }

            // Ëé∑ÂèñÂú∞ÂõæÁªèÁ∫¨Â∫¶
            this.location = [];
            HotelList.data.data.forEach(item => {
                this.location.push({
                    x: item.location.latitude,
                    y: item.location.longitude,
                })
            })
            console.log(this.location);
            // Âä†ËΩΩÂú∞Âõæ
            if(this.location.length){
                this.mapLoad()
            }
        },

        // ÂΩìÂâçÈ°µÂèëÁîüÂèòÂåñ
        currentChange(newCurrent) {
            this.isBox = false
            // ÁßªÂä®Âà∞ÈÖíÂ∫óÂàóË°®ÁöÑ‰∏äÊñπ
            let HotelHeight = document.querySelector('.HotelList').offsetTop
            window.scrollTo({ 
                top: HotelHeight, 
                behavior: "smooth" 
            });
            // ÂÖàÊ†πÊçÆÈ°µÊï∞Ëé∑ÂèñÈÖíÂ∫óÁöÑÊù°Êï∞
            this.currentPage = newCurrent
            this.limit = newCurrent * 10
            this.getHotelList()
        },

        // È´òÂæ∑Âú∞Âõæ
        mapLoad() {
            this.map.remove(this.markers);
            this.markers = [];

            // ÈÅçÂéÜ-ÂàõÂª∫ÁÇπÂÆû‰æã
            this.location.forEach((item, index) => {
                var markerContent =
                    ""
                    +
                    '<div class="custom-content-marker">'
                    +
                    '<img src="https://webapi.amap.com/theme/v1.3/markers/b/mark_bs.png">'
                    +
                    `<div class="close-btn" onclick="clearMarker()">${index + 1}</div>`
                    +
                    '</div>';
                // console.log('markerContent', markerContent);
                var maker = new AMap.Marker({
                    content: markerContent,
                    position: [item.y, item.x],
                })
                this.markers.push(maker)
            })
            this.map.panTo([this.location[0].y, this.location[0].x])
            // Ê∑ªÂä†ÁÇπ
            this.map.add(this.markers)
            this.isMap = false
        },

        // Ëé∑ÂèñÂú∞ÂõæÂΩìÂâçË°åÊîøÂå∫
        async getInfoCity() {
            // Ëé∑ÂèñÁî®Êà∑ÂΩìÂâçÂÆö‰ΩçÂüéÂ∏Ç
            await this.map.getCity(async (info) => {
                // console.log(info);
                // ‰øÆÊîπÂéüÊúâÂèÇÊï∞,ËÄå‰∏çË∑≥ËΩ¨È°µÈù¢
                this.urlCityName = info.city

                // Ëé∑ÂèñÂüéÂ∏Çid
                await this.$axios({
                    url: '/cities?name=' + this.urlCityName
                }).then(res => {
                    console.log(res);
                    if (!res.data.data.length) {
                        this.$message({
                            showClose: true,
                            message: `ÊêúÁ¥¢‰∏çÂà∞ÂΩìÂâçÂüéÂ∏Ç`,
                            type: 'error'
                        });
                        return
                    }
                    // console.log(res.data.data[0].id);
                    this.cityId = res.data.data[0].id
                })

                this.$router.push({
                    path: this.$route.path,
                    query: Object.assign({}, this.$route.query, { cityName: this.urlCityName })
                })
                this.getHotelList()
                this.getScenice()
                this.$message({
                    showClose: true,
                    message: `ÂΩìÂâçÂÆö‰ΩçÂüéÂ∏Ç ${info.city}`,
                    type: 'success'
                });
            });
        }
    }
};
